.range__scale {
  display: grid;
  grid-template-columns: 79px 1fr 80px;
  align-items: center;
}
.range__button {
  font-size: 16px;
  line-height: 20px;
  color: hsl(0, 0%, 0%);
  text-transform: uppercase;
  position: relative;
  outline: none;
  background-color: transparent;
  border: none;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
}
.range__button:hover {
  background-image: none;
  position: relative;
}
.range__button:hover::before {
  position: absolute;
  bottom: 0;
  left: 1px;
  right: 1px;
  height: 1px;
  background-color: hsla(0, 0%, 0%, 0.4);
  content: "";
}
.range__button:active,
.range__button:focus-visible {
  background-image: none;
}
.range__button:active::before,
.range__button:focus-visible::before {
  display: none;
}
.range__button:hover ~ .range__bar .range__toggle {
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
}
.range__button:active ~ .range__bar .range__toggle,
.range__button:focus-visible ~ .range__bar .range__toggle {
  filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
  border: 1px solid hsl(97, 53%, 47%);
}
.range__button--was {
  text-align: end;
}
.range__button--become {
  grid-column: 3/4;
  text-align: left;
}
.range__bar {
  position: relative;
  width: 85px;
  height: 14px;
  background-color: hsl(0, 0%, 100%);
  border: 1px solid hsl(0, 0%, 80%);
  border-radius: 6px;
  display: flex;
  align-items: center;
  padding-left: 6px;
  padding-right: 6px;
  box-sizing: border-box;
  margin-left: 19px;
  margin-right: 17px;
  grid-column: 2/3;
  grid-row: 1/2;
}
.range__toggle {
  padding: 0;
  position: absolute;
  content: "";
  bottom: 0;
  top: 0;
  margin-top: auto;
  margin-bottom: auto;
  left: 5px;
  width: 36px;
  background-color: hsl(97, 53%, 47%);
  border-radius: 6px;
  border: none;
  height: 6px;
  cursor: pointer;
  filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));
  outline: none;
}
.range__toggle:hover {
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
}
.range__toggle:active,
.range__toggle:focus-visible {
  filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
  border: 1px solid hsl(97, 53%, 47%);
}
@media (min-width: 768px) {
  .range__bar {
    width: 427px;
    height: 6px;
    background-color: hsl(0, 0%, 80%);
    border: none;
    padding: 0;
    margin: 0;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
  }
  .range__toggle {
    left: calc(50% - 18px);
    width: 33px;
    height: 33px;
    border-radius: 50%;
    background-color: hsl(0, 0%, 100%);
  }
  .range__toggle::after {
    content: "";
    width: 8px;
    height: 8px;
    background-color: hsl(97, 53%, 47%);
    border-radius: 50%;
    position: absolute;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
  }
  .range__button {
    position: relative;
    font-size: 20px;
    line-height: 29.64px;
    justify-self: center;
  }
  .range__scale {
    margin: 0;
    grid-template-columns: 73px 1fr 70px;
  }
}
