.catalog__list {
  margin: 0;
  padding: 0;
  position: relative;
}
.catalog__item {
  display: grid;
  grid-template-columns: 140px 1fr;
  grid-template-rows: min-content 13px min-content;
  font-size: 16px;
  line-height: 20px;
  align-items: end;
  padding-top: 22px;
  padding-bottom: 22px;
  position: relative;
}
.catalog__item::before {
  position: absolute;
  right: -8.8%;
  left: -9%;
  top: 0;
  height: 1px;
  background-color: hsl(0, 0%, 95%);
  content: "";
}
.catalog__item:last-of-type::after {
  position: absolute;
  right: -7%;
  left: -7%;
  bottom: -2px;
  height: 1px;
  background-color: hsl(0, 0%, 95%);
  content: "";
}
.catalog__picture {
  grid-column: 1/2;
  grid-row: 1/3;
  height: auto;
  width: 140px;
}
.catalog__content-container {
  grid-column: 2/3;
  grid-row: 1/2;
  align-self: start;
  margin-top: 2px;
}
.catalog__link-title {
  display: block;
  color: hsl(0, 0%, 13%);
  text-transform: uppercase;
  text-decoration: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  width: 100px;
  margin: 0;
  margin-bottom: 14px;
}
.catalog__list-ineer {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  margin: 0;
}
.catalog__item-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 15px;
}
.catalog__description {
  margin: 0;
  text-align: end;
}
.catalog__description--weight {
  margin-right: 2px;
}
.catalog__button {
  grid-column: 1/-1;
  grid-row: 3/4;
}
.catalog__container-title {
  position: relative;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  padding-top: 59px;
  color: hsl(0, 0%, 7%);
}
.catalog__container-title::after,
.catalog__container-title::before {
  position: absolute;
  content: "";
  top: 19px;
  right: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  width: 30px;
  height: 2px;
  background-color: hsl(0, 0%, 83%);
}
.catalog__container-title::before {
  transform: rotate(90deg);
}
.catalog__item-container {
  grid-template-columns: 100%;
  grid-template-rows: min-content 8px min-content 21px min-content;
}
.catalog__container-desc {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  margin: 0;
  text-align: center;
  grid-row: 3/4;
}
.catalog__container-link {
  grid-row: 5/6;
  background-color: hsl(0, 0%, 95%);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: hsl(0, 0%, 27%);
  text-transform: uppercase;
  min-height: 40px;
  padding: 10px;
  box-sizing: border-box;
  outline: none;
}
.catalog__container-link:hover {
  background-color: hsl(0, 0%, 92%);
}
.catalog__container-link:active,
.catalog__container-link a:focus-visible {
  color: hsla(0, 0%, 27%, 0.3);
}
