  padding-top: 25px;
  padding-bottom: 38px;
}
.container--information-ineer {
  background-color: hsl(0, 0%, 95%);
}
@media (min-width: 768px) {
  .container__width {
    width: 90%;
    min-width: 708px;
  }
  .container__width--logo {
    width: auto;
    min-width: auto;
  }
  .container__width--presentation-link {
    width: fit-content;
    margin-left: 0;
  }
  .container--exemple {
    padding-top: 43px;
    padding-bottom: 47px;
    position: relative;
    background-color: transparent;
    background-image: linear-gradient(to bottom, hsl(0, 0%, 100%) 57.3%, hsl(0, 0%, 95%) 57.3%, hsl(0, 0%, 95%));
  }
  .container--exemple::after {
    position: absolute;
    width: 100%;
    height: 1px;
    left: 0;
    top: 0;
    background-color: hsl(0, 0%, 95%);
    content: "";
  }
  .container--about-program {
    padding-top: 46px;
  }
}
@media (min-width: 1440px) {
  .container__width {
    width: 80%;
    min-width: 1220px;
    margin-left: auto;
    margin-right: auto;
  }
  .container__width--logo {
    width: auto;
    min-width: auto;
  }
  .container__width--presentation-title {
    width: 414px;
    min-width: auto;
    margin-left: 190px;
  }
  .container--about-program {
    padding-top: 71px;
  }
  .container--exemple {
    padding-top: 26px;
    padding-bottom: 55px;
    background-image: linear-gradient(to bottom, hsl(0, 0%, 100%) 32%, hsl(0, 0%, 95%) 32%, hsl(0, 0%, 95%));
  }
  .container--exemple::after {
    display: none;
  }
  .container__width--information {
    width: 565px;
    min-width: min-content;
  }
  .container__width--catalog {
    min-width: 1223px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  width: 100%;
  min-height: 40px;
  border: none;
  background-color: hsl(97, 53%, 47%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: hsl(0, 0%, 100%);
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
}
.button:hover {
  background-color: hsl(97, 57%, 43%);
}
.button:active,
.button:focus-visible {
  color: hsla(0, 0%, 100%, 0.3);
}
.button--inner {
  font-size: 16px;
  line-height: 20px;
}
.button--presentation {
  width: 85%;
  min-width: 280px;
}
@media (min-width: 768px) {
  .button {
    width: fit-content;
  }
  .button--presentation {
    min-height: 54px;
    min-width: 245px;
  }
  .button--inner {
    width: 100%;
  }
  .button--form {
    min-height: 50px;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .button--presentation {
    min-width: 244px;
  }
  .button--inner {
    width: 99%;
  }
  .button--form {
    min-height: 52px;
    width: 97%;
    min-width: 568px;
  }
}
.information__contact-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 26px;
  padding-bottom: 26px;
  box-sizing: border-box;
}
.information__title {
  text-transform: uppercase;
  font-weight: 400;
  margin: 0;
  font-size: 16px;
  line-height: 20px;
}
.information__content {
  flex-grow: 1;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}
.information__content-adress {
  margin: 0;
  padding-left: 17px;
  box-sizing: border-box;
}
.information__cart {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 362px;
}
.information__img-iframe {
  border: none;
  display: block;
  width: 100%;
  height: 100%;
}
.information__img-card {
  display: none;
}
.information__card--nojs {
  display: none;
}
.information__img-iframe--nojs {
  display: none;
}
@media (min-width: 768px) {
  .information__contact-container {
    justify-content: space-between;
    padding-top: 59px;
    padding-bottom: 59px;
    padding-right: 40px;
    padding-left: 40px;
  }
  .information__title {
    font-size: 20px;
    line-height: 26px;
    width: 180px;
    flex-grow: 0;
  }
  .information__content {
    flex-grow: 0;
    text-align: end;
    width: 180px;
  }
  .information__content-adress {
    font-size: 16px;
  }
  .information__content-adress:first-of-type {
    margin-bottom: 20px;
  }
  .information__cart {
    height: 400px;
  }
}
@media (min-width: 1440px) {
  .information {
    position: relative;
    width: 100%;
    height: 400px;
  }
  .information__contact-container {
    min-height: 198px;
    height: max-content;
    position: absolute;
    left: 110px;
    top: 96px;
    background-color: hsl(0, 0%, 100%);
    z-index: 1;
    padding-top: 56px;
    padding-bottom: 56px;
    padding-left: 80px;
    padding-right: 80px;
  }
  .information__title {
    flex-grow: 1;
    width: 50%;
    box-sizing: border-box;
    padding-right: 20px;
  }
  .information__content {
    text-align: left;
    flex-grow: 1;
    width: 50%;
    margin-bottom: 5px;
  }
  .information__content-adress {
    padding-left: 47px;
  }
}
